<script setup>
import { ref } from 'vue'

const darkMode = ref(false)

function toggleDarkMode() {
    const html = document.querySelector('html')
    const body = document.querySelector('body')

    if (darkMode.value) {
        darkMode.value = false
        body.classList.add('bg-blue-50')
        body.classList.remove('bg-slate-800')
        return html.classList.remove('dark');
    }

    darkMode.value = true
    body.classList.remove('bg-blue-50')
    body.classList.add('bg-slate-800')
    return html.classList.add('dark');
}
</script>

<template>
    <div class="flex justify-between">
        devfinder
        <div class="w-1/4 flex justify-around uppercase lg:w-1/6">
            {{ darkMode ? 'dark' : 'light' }}
            <button @click="toggleDarkMode">
                <svg v-if="darkMode" xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
                    <path stroke-linecap="round" stroke-linejoin="round" d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z" />
                </svg>
                <svg v-else xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
                    <path stroke-linecap="round" stroke-linejoin="round" d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z" />
                </svg>
            </button>
        </div>
    </div>
</template>